<template>
  <el-container>
    <!-- <div v-wechat-title="$route.meta.title">门诊缴费</div> -->
    <!-- <el-header></el-header> -->
    <el-main>
      <!-- 基本信息 -->
      <div class="title" style="margin-top:10px">
        <div class="title-img"></div>
        <div class="title-font">
          <span>基本信息</span>
        </div>
      </div>
      <el-card class="box-card">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">就诊人</div>
              <div class="grid-content bg-purple">单据号</div>
              <div class="grid-content bg-purple">支付金额</div>
              <div class="grid-content bg-purple">开单科室</div>
              <div class="grid-content bg-purple">开单人</div>
              <div class="grid-content bg-purple">开单日期</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div class="grid-content bg-purple-light">
                <span>杨振(0005674)</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>00024578</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span style="color:#FF4848;">￥</span><span style="color:#FF4848;">0.97</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>急诊科</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>张三</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>2020-04-01 10:10</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
    <!-- 费用汇总 -->
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>单据信息</span>
        </div>
      </div>
            <el-card class="box-card">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">发票号</div>
              <div class="grid-content bg-purple">微信交易单号</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div class="grid-content bg-purple-light">
                <span>ZF634154612215596484</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>847882XXDFSDF4574</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>费用汇总</span>
        </div>
      </div>
            <el-card class="box-card">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">发票号</div>
              <div class="grid-content bg-purple">微信交易单号</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div class="grid-content bg-purple-light">
                <span>ZF634154612215596484</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>847882XXDFSDF4574</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <!-- 费用明细 -->
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>费用明细</span>
        </div>
      </div>
      <el-card class="box-card payz">
        <el-table
          :data="tableData"
          show-summary
          :summary-method="getSummaries"
          style="font-size: 13px;color:#333;"
        >
          <el-table-column prop="name" label="姓名" width="111px"></el-table-column>
          <el-table-column prop="univalence" label="单价(元)"></el-table-column>
          <el-table-column prop="unit" label="数量.单位"></el-table-column>
          <el-table-column prop="sum" label="金额(元)"></el-table-column>
        </el-table>
      </el-card>
    </el-main>
    <!--  -->
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';

.el-col-16 {
    text-align: right;
}
.fontr {
    text-align: left ; 
     padding-right: 0; 
}
</style>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          name: "自费西药",
          univalence: "0.3",
          unit: "一支",
          sum: 0.3
        },
        {
          name: "氨加黄敏胶囊",
          univalence: "0.032",
          unit: "一粒",
          sum: 0.032
        },
        {
          name: "葡萄糖",
          univalence: "0.5",
          unit: "一瓶",
          sum: 0.5
        },
        {
          name: "小儿氨酚黄那敏",
          univalence: "0.14",
          unit: "一盒",
          sum: 0.22
        }
      ]
    };
  },
  methods: {
    // 金额列求和
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = "总金额：";
        } else if (index === 3) {
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += "元";
          } else {
            sums[index] = "N/A";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  }
};
</script>



